@import url('https://fonts.googleapis.com/css2?family=Jua&display=swap');

.app {
  width: 860px;
  height: 1200px;
  background-color:rgb(251, 249, 244);
  margin: 0 auto;
  padding: 0 20px;
}

h1 {
  font-family: 'Jua';
  text-align: center;
  padding-top: 20px;
  font-size: 2.5em;
  margin: 0;
}
ul {
  display: flex;
  justify-content: space-evenly;
  padding: 5px 0;
}
.app > ul {
  border-bottom: 1px solid #ccc;
}
li {
  list-style: none;
  text-decoration: none;
  padding: 5px;
  font-size: 1.2em;
  font-weight: bolder;
}
.app > ul > li{
  border-right: 1px solid #ccc;
}
.app > ul > li:last-child {
  border-right: none;
}
li .NavLink {
  padding: 0 100px;
  text-decoration: none;
  color: #000;
}

#intro {
  padding: 150px;
}
#intro > img {
  display: block;
  margin: 0 auto;
}
#intro > h2 {
  font-family: 'Jua';
  text-align: center;
  font-size: 1.6em;
  margin: 40px 0;
}
#intro > p {
  font-family: 'Jua';
  text-align: center;
  font-size: 1.3em;
  margin: 0;
}

#menuList {
  /* padding-top: 20px; */
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}
#menuList > li {
  width: 20%;
  padding: 40px;
  margin: 10px;
  text-align: center;
  border: 1px solid #ccc;
}
#menuList li #menuNav  {
  text-decoration: none;
  color: #000;
  width: 100%;
}
#product {
  text-align: center;
}
#productImg{
  padding-bottom: 30px;
}

#commuNav {
  text-decoration: none;
  color: #000;
  width: 100%;
  border-bottom: 1px solid #ccc;
}
#community {
  padding-top: 50px;
}
#community > h3 {
  font-family: 'Jua';
  text-align: center;
  font-size: 1.6em;
}
#community > #commu_img {
  display: flex;
  justify-content: center;
  padding-bottom: 50px;
}
#community > #commu_img > img {
  width: 60%;
}

